<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" :rules="paramRules" size="small">
        <el-row :gutter="20">
          <el-col :span="5">
            <el-form-item :label="$t('simMag.addressId')" prop="addressIds">
              <ct-async-select
                v-model="qryParam.addressIds"
                :placeholder="selectPh('simMag.addressId')"
                url="/prodInst/getSupplierListForQuery"
                method="get"
                label-key="attrValueName"
                value-key="attrValue"
              ></ct-async-select>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('traffic.groupId')" prop="accNum">
              <el-input
                v-model="qryParam.accNum"
                type="number"
                :placeholder="inputPh('traffic.groupId')"
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item :label="$t('traffic.billCycle')" prop="billCycle">
              <BillCycleSelect v-model="qryParam.billCycle" />
            </el-form-item>
          </el-col>
          <el-col :span="9">
            <el-form-item label-width="0">
              <div class="search-button-group">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="loadData(false)"
                  >{{ $t('common.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
              </div>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        class="container-table"
        :data="tableData"
        :border="true"
        height="100%"
        width="100%"
      >
        <el-table-column
          prop="accNum "
          :label="$t('traffic.groupId')"
          show-overflow-tooltip
          resizable
          min-width="110"
        >
          <template slot-scope="scope">
            <a class="cor-blue" @click="toDetail(scope.row)">{{
              scope.row.appProdInstDto.accNum
            }}</a>
          </template>
        </el-table-column>
        <el-table-column
          prop="appCustomerDto.custName"
          :label="$t('traffic.useCustName')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column :label="$t('traffic.memberMag')" min-width="110">
          <template slot-scope="scope">
            <a
              class="cor-blue el-icon-user"
              @click="toMemberMag(scope.row.appProdInstDto.accNum)"
            ></a>
          </template>
        </el-table-column>
        <el-table-column :label="$t('traffic.usageQuery')" min-width="110">
          <template slot-scope="scope">
            <a
              class="cor-blue el-icon-pie-chart"
              @click="toUsageQuery(scope.row.appProdInstDto.accNum)"
            ></a>
          </template>
        </el-table-column>
        <el-table-column
          prop="offerName"
          :label="$t('traffic.billCycle')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
        <el-table-column
          prop="offerUsageMb"
          :label="$t('traffic.poolSize')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <!-- <el-table-column
          prop="totalUsageMb"
          label="当月可用量（MB)"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column> -->
        <el-table-column
          prop="billUsageMb"
          :label="$t('traffic.avaMonth')"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="overUsageMb"
          :label="$t('traffic.remainMonth')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch.js'
export default {
  mixins: [langMixin],
  data() {
    return {
      loading: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: [],
      querySupplierIspListRes: [],
      qryParam: {
        addressIds: '121215042193',
        accNum: '',
      },
    }
  },
  computed: {
    paramRules() {
      return {
        addressIds: [
          { required: true, trigger: 'blur', message: this.selectPh('simMag.addressId') },
        ],
      }
    },
  },
  methods: {
    async loadData(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        addressIds: [this.qryParam.addressIds],
        ...this.page,
      }
      this.loading = true
      const res = await fetch('/flowPool/qryBackFlowPoolList', param, {
        method: 'post',
      }).finally(() => {
        this.loading = false
      })
      this.tableData = res.result || []
      this.page.total = res.rowCount || 0
    },
    toDetail(row) {
      this.$router.push({
        path: '/infoQuery/poolDetail',
        query: { row: JSON.stringify(row) },
      })
    },
    toMemberMag(accNum) {
      this.$router.push({
        path: '/infoQuery/backMemberMag',
        query: { grpNumber: accNum },
      })
    },
    toUsageQuery(accNum) {
      this.$router.push({ path: '/infoQuery/usageQuery', query: { accNum } })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.loadData(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.loadData(true)
    },
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.$nextTick(() => this.loadData(false))
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
</style>
